










import Vue from "vue";

export default Vue.component("cell", {
  components: {},
  props: {
    isUpper: Boolean,
    isShut: Boolean,
    index: Number
  },
  methods: {
    shut() {
      this.checkCookie();
      return this.$store.dispatch("shut", this.index+1);
    },
    checkCookie() {
      this.$store.commit('checkSignedIn');
      if (!this.$store.state.signedIn) {
        this.$router.push("login");
      } 
    }
  }
});
